import React from "react";
import { Box, Stack, Typography } from "@mui/material";
// People
// import { PersonSearch, School } from "@mui/icons-material";

// UI
const OrganizerCard = ({ organizer }) => {
  // organizernameschool
  const { name, organization } = organizer;
  return (
    // Box
    <Stack
      sx={{
        width: "100%",
      }}
      direction={"row"}
    >
      {/* Box */}
      <Box
        sx={{
          p: 0.5,
          width: "30%",
          borderRadius: "100px 0 0 100px",
          border: "2px solid",
          borderRight: "1px solid",
          bgcolor: "#eef2ff",
          borderColor: "#8d9cc3",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        {/* PeopleIcon */}
        {/* <PersonSearch sx={{ color: "#0e477e" }} /> */}
        {/* Typography */}
        <Typography variant="h6">{name}</Typography>
      </Box>
      {/* Box */}
      <Box
        // sxBox
        sx={{
          p: 0.5,
          flexGrow: 1,
          borderRadius: "0 100px 100px 0",
          border: "2px solid",
          borderLeft: "1px solid",
          // bgcolor: "#efefef",
          borderColor: "#8d9cc3",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          boxShadow: "inset 0px 0px 9px 0px rgba(0, 0, 0, 0.2)",
        }}
      >
        {/* <School sx={{ color: "#0e477e" }} /> */}
        {/* Typography */}
        <Typography variant="body1">{organization}</Typography>
      </Box>
    </Stack>
  );
};

export default OrganizerCard;
