// ReactMUI
import React, { useEffect, useState } from "react";
import {
  Container,
  Typography,
  Grid,
  Toolbar,
  AppBar,
  Box,
  Button,
  Paper,
  Stack,
} from "@mui/material";
import HomeHeader from "../components/HomeHeader";
import { useTheme } from "@emotion/react";
import TravelCard from "../components/TravelCard";
import data from "../api/data";
import NotificationPanel from "../components/NotificationPanel";
import icons from "../icons";

/**
 * 
 * content : 
 * message : ""
 */
function copyToClip(content) {
  var aux = document.createElement("input");
  aux.setAttribute("value", content);
  document.body.appendChild(aux);
  aux.select();
  document.execCommand("copy");
  document.body.removeChild(aux);
}

// 
function IntroPage({ notes, setNotes }) {
  const theme = useTheme();
  const [articles, setArticles] = useState([]);
  useEffect(() => {
    data.getArticles().then((al) => {
      setArticles(al);
    });
  }, []);
  const articleTypes = [
    "",
    "",
    "",
    "",
    "",
  ];
  return (
    <>
      <AppBar
        sx={{
          background: theme.home.bl,
        }}
      >
        <Container sx={{ p: 0 }}>
          <Toolbar>
            <HomeHeader />
          </Toolbar>
        </Container>
      </AppBar>
      <Toolbar />
      <Container>
        <Box sx={{ mt: "1.5rem" }} />
        <Typography variant="h4" align="center">
          
        </Typography>
        <Typography variant="h4" align="center">
          
        </Typography>
        <Box sx={{ mt: "0.5rem" }} />
        <Typography variant="h6" align="center">
          2023122831 | 
        </Typography>
        <NotificationPanel notes={notes} setNotes={setNotes} />
        <Typography
          color={"#222222"}
          sx={{ mt: 1, textAlign: "center" }}
          variant="h6"
        >
          
        </Typography>
        <img
          src={"./images/loc/locations.jpg"}
          style={{
            maxWidth: "100%",
            height: "auto",
            objectFit: "cover",
          }}
          alt={""}
        />
        <Box
          sx={{
            mt: 2,
            display: "flex",
            flexDirection: "row",
            flexWrap: "wrap",
            justifyContent: "space-evenly",
            gap: 2,
          }}
        >
          <HandBook />
          <ScheduleApp />
          <CsigPub />
        </Box>
        {articleTypes.map((atype) => {
          const arts = articles.filter((e) => e.group === atype);
          return (
            <div key={atype}>
              {arts.length ? (
                <>
                  <Typography sx={{ mt: 3, textAlign: "center" }} variant="h5">
                    {atype}
                  </Typography>
                  <Grid container spacing={4}>
                    {arts.map((item, index) => (
                      <Grid item xs={12} sm={6} md={4} key={item.title}>
                        <TravelCard data={item} />
                      </Grid>
                    ))}
                  </Grid>
                </>
              ) : null}
            </div>
          );
        })}
        <Toolbar />
      </Container>
    </>
  );
}

export default IntroPage;
function ScheduleApp() {
  return (
    <Paper
      sx={{
        display: "flex",
        flexDirection: "row",
        flexWrap: "wrap",
        justifyContent: "space-around",
        alignItems: "center",
        p: 1,
        gap: 2,
      }}
    >
      <Stack>
        <Typography variant="h6" textAlign={"center"}>
          <strong></strong>
        </Typography>
        <Button
          onClick={() => {
            const link =
              "https://prod-6g5nnh0zce8b86b8-1322323920.tcloudbaseapp.com";
            try {
              try {
                navigator.clipboard.writeText(link);
                alert("");
              } catch {
                copyToClip(link);
                alert("");
              }
            } catch (error) {
              alert("");
            }
          }}
          startIcon={icons.get("")}
        >
          
        </Button>
        <img
          src={"./images/qrcode/.png"}
          style={{
            height: "8rem",
            width: "auto",
            objectFit: "cover",
            // boxShadow: `0px 2px 6px 0px rgba(0, 0, 0, 0.5)`,
            display: "block",
          }}
          alt={""}
        />
      </Stack>
      <img
        src={"./images/logo/.jpg"}
        style={{
          width: "35%",
          maxWidth: "400px",
          objectFit: "cover",
          boxShadow: `0px 2px 6px 0px rgba(0, 0, 0, 0.5)`,
          display: "block",
        }}
        alt={""}
      />
    </Paper>
  );
}

